body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App-header {
  background-color: #222;
  padding: 0 20px;
  color: white;
  white-space: nowrap;
}

.App-title {
  font-size: 1.5em;
  margin-right: 40px;
  display: inline-block;
  vertical-align: middle;
}

.header-btn {
  display: inline-block;
  vertical-align: middle;
  margin: 10px;
  border: none;
  background: white;
  font-size: 0.75em;
  padding: 0.5em 1.5em;
}

.lists {
  white-space: nowrap;
}

.list {
  display: inline-block;
  vertical-align: top;
  width: 8px;
}

.list-heading {
  text-align: center;
  font-size: 6px;
}

.list-ul {
  padding: 0;
  margin: 0;
}

.list-li {
  margin: 0;
  font-size: 4px;
  text-align: center;
  list-style: none;
  line-height: 2em;
}


